<template>
	<div class="wrapper">
		<div class="back-wrap">
			<div class="row mt-login">
				<div class="col-md-4"></div>
				<div class="col-md-4">
					<div class="card card-login">
						<div class="card-body pd-login">
							<h3 class="card-title text-center">
								<img :src="require('@/assets/images/logo.png')" style="width: 50%; margin-bottom: 30px;">
							</h3>
							<div>
								<div class="form-group">
									<label class="font-weight-bold">Username</label>
									<input class="form-control form-control-lg" type="text" v-model="username" @keyup.enter="login"/>
								</div>
								<div class="form-group">
									<label class="font-weight-bold">Password</label>
									<div class="input-group">
										<input :type="(!state.password) ? 'password' : 'text'" class="form-control form-control-lg" v-model="password" @keyup.enter="login" id="password-field" />
										<div class="input-group-append">
											<button type="button" v-show="!state.password" @click="state.password = true" class="btn" style="background: #eee"><i class="fas fa-eye"></i></button>
											<button type="button" @click="state.password = false" v-show="state.password" class="btn" style="background: #eee"><i class="fas fa-eye-slash"></i></button>
										</div>
									</div>
								</div>
								<div class="text-center mt-4">
									<button @click="login" class="btn btn-lg btn-primary btn-block">Login</button>
								</div>
								<!-- <p class="mt-3">Belum Punya Akun ? <router-link to="/register">Daftar Disini</router-link></p> -->
							</div>
						</div>
					</div>	
				</div>
				<div class="col-md-4"></div>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">

	import alertMixin from '@/mixins/alert'

	export default{
		name : 'Login',
		mixins: [
		alertMixin,
		],
		data(){
			return{
				state: {
					password: false,
				},
				username : null,
				password : null,
			}
		},
		mounted(){
			if(this.$store.state.auth.authenticated){
				this.$router.push('/home');
			}
		},
		methods : {
			async login(){

				if(this.username == "" || this.password == ""){
					this.$swal('Oops', 'Semua isian harus diisi', 'error');
				}else{
					this.showLoading();
					await this.$store.dispatch("auth/authRequest", {
						username: this.username,
						password: this.password,
					});
					this.hideLoading();

					let authenticated = await this.$store.state.auth.authenticated;
					if(authenticated){
						this.$router.go("/home");
					}else{
						let message = await this.$store.state.auth.status;
						if(message){
							this.showAlert(message, 'error');
						}else{
							this.showAlert("Login Gagal", 'error');
						}
					}
				}
			}
		}
	}
</script>

<style scoped="">
.btn{
	border:none;
}
.wrapper{
	overflow-x: hidden;
	min-height: 100vh;
	background: var(--main-color);
}
.back-wrap{
	overflow-x: hidden;
	position: fixed;
	background: var(--main-color);
	width: 100%;
	min-height: 100vh;
	overflow-y: auto;
}
.mt-login{
	margin-top: 100px;
	background: transparent;
}
.pd-login{
	padding: 50px;
}
.card-login{
	box-shadow: 10px 10px 10px 10px rgba(0,0,0,.1);
}
</style>